function ContentSection({ title, items, onSelect }: { title: string, items: any[], onSelect: (h: number) => void }) {
  const [activeIndex, setActiveIndex] = useState(0);
  const activeItem = items[activeIndex];

  const handleItemClick = (index: number) => {
    setActiveIndex(index);
    onSelect(items[index].hue);
  };

  return (
    <section>
      <motion.h2 
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        viewport={{ once: true }}
        className="text-xs font-bold tracking-[0.2em] text-white/30 mb-8 uppercase"
      >
        {title}
      </motion.h2>

      <div className="flex flex-wrap gap-x-10 gap-y-4 mb-16 border-b border-white/5 pb-6">
        {items.map((item, idx) => (
          <button
            key={item.id}
            onClick={() => handleItemClick(idx)}
            className={`text-2xl md:text-4xl font-medium tracking-tight transition-all duration-700 ${
              idx === activeIndex 
                ? "text-white opacity-100 scale-100" 
                : "text-white/20 opacity-60 scale-95 hover:text-white/40"
            }`}
          >
            {item.company || item.title}
          </button>
        ))}
      </div>

      <div className="relative min-h-[400px]">
        <AnimatePresence mode="wait">
          <motion.div
            key={activeItem.id}
            initial={{ opacity: 0, y: 10, filter: "blur(10px)" }}
            animate={{ opacity: 1, y: 0, filter: "blur(0px)" }}
            exit={{ opacity: 0, y: -10, filter: "blur(10px)" }}
            transition={CONFIG.transition}
            className="w-full grid grid-cols-1 md:grid-cols-2 gap-12 items-start"
          >
            <div className={`aspect-video md:aspect-square rounded-2xl overflow-hidden bg-gradient-to-br ${activeItem.accent} bg-opacity-10 relative border border-white/5 shadow-2xl`}>
                <div className="absolute inset-0 bg-black/60 backdrop-blur-sm" />
                <div className="absolute inset-0 flex items-center justify-center">
                    <p className="text-white/20 font-mono uppercase tracking-widest text-xs">Project Visual</p>
                </div>
            </div>

            <div className="flex flex-col gap-6 pt-2">
              <div>
                <h3 className="text-3xl font-bold text-white mb-2">{activeItem.company || activeItem.title}</h3>
                <p className={`text-lg bg-clip-text text-transparent bg-gradient-to-r ${activeItem.accent} font-medium`}>
                  {activeItem.role || activeItem.tech}
                </p>
              </div>
              
              <p className="text-lg text-white/60 leading-relaxed font-light">
                {activeItem.desc}
              </p>

              {activeItem.date && (
                <div className="mt-4 pt-6 border-t border-white/10">
                  <span className="font-mono text-xs text-white/30">{activeItem.date}</span>
                </div>
              )}
            </div>
          </motion.div>
        </AnimatePresence>
      </div>
    </section>
  );
}

